<script setup>
import {ref, onMounted} from 'vue'
import { useRoute, useRouter } from 'vue-router'

onMounted( () => {
  animateBlock()
  makeItVisible(2000)
})

const router = useRouter()
const route = useRoute()
const redirect = route.redirectedFrom ? route.redirectedFrom.fullPath : '/'

const isVisible = ref(false)

function makeItVisible (timeout) { setTimeout( () => isVisible.value = true, timeout) }


const isBlockLifted = ref(false)
console.log('router', router)
console.log('route', route)
function animateBlock() {
  const t = setTimeout( () => {
    isBlockLifted.value = true
    clearTimeout(t)
  },500)
}
</script>


